import samples from './samples';
import runTimer from '../taskTimer';

export default function auding() {
  const samplesArr = samples();
  const sample = samplesArr[Math.floor(Math.random() * samplesArr.length)];
  const way = sample.src.split('/');
  let solution = way[way.length - 1];
  solution = solution.substr(0, solution.length - 4);

  function play() {
    sample.play();
  }

  function drawTask() {
    const taskBlock = document.createElement('div');
    taskBlock.setAttribute('class', 'game__modal game__modal--task');
    taskBlock.innerHTML = `
      <div class="task__timer">10</div>
      <div class="task__player"></div>
      <div class="task__text">Веди услышанное слово</div>
      <input class="game__input"type="text">
      <button class="game__btn game__btn--answer">Ответить</button>
    `;
    document.querySelector('.game__wrapper').appendChild(taskBlock);
    document.querySelector('.task__player').addEventListener('click', play);
    setTimeout(() => {
      sample.play();
    }, 600);
    runTimer(15);
  }

  function checkSolution() {
    let result = 'enemyRight';
    const playerSolution = document.querySelector('.game__input').value;
    if (playerSolution === solution && document.querySelector('.task__timer').innerHTML === 'Время вышло!') {
      result = 'bothRight';
    } else if (playerSolution === solution) {
      result = 'playerRight';
    }
    return result;
  }

  return { draw: drawTask, check: checkSolution };
}
