import runTimer from '../taskTimer';

export default function task() {
  function randomInt(min, max) {
    return Math.floor(Math.random() * (max - min)) + min;
  }

  const signs = ['+', '-', '*'];
  const sign = signs[randomInt(0, 3)];
  let first = 0;
  let second = 0;
  if (sign === '*') {
    first = randomInt(3, 11);
    second = randomInt(3, 11);
  } else if (sign === '-') {
    const f = randomInt(11, 21);
    const s = randomInt(11, 21);
    if (f > s) {
      first = f;
      second = s;
    } else {
      first = s;
      second = f;
    }
  } else {
    first = randomInt(11, 21);
    second = randomInt(11, 21);
  }

  function solve() {
    if (sign === '*') {
      return [first, sign, second, first * second];
    }
    if (sign === '-') {
      return [second, sign, first, first - second];
    }
    return [first, sign, second, first + second];
  }

  const solution = solve()[3];

  function drawTask() {
    const taskBlock = document.createElement('div');
    taskBlock.setAttribute('class', 'game__modal game__modal--task');
    taskBlock.innerHTML = `
      <div class="task__timer">10</div>
      <div class="task__text">Вычисли пример :</div>
      <div class="task__example">
        <span class="task__text">${first} ${sign} ${second} = </span>
        <input class="game__input game__input--math" type="text">
      </div>
      <button class="game__btn game__btn--answer">Ответить</button>
    `;
    document.querySelector('.game__wrapper').appendChild(taskBlock);
    runTimer(10);
  }

  function checkSolution() {
    let result = 'enemyRight';
    const playerSolution = document.querySelector('.game__input').value;
    if (+playerSolution === solution && document.querySelector('.task__timer').innerHTML === 'Время вышло!') {
      result = 'bothRight';
    } else if (+playerSolution === solution) {
      result = 'playerRight';
    }
    clearInterval(runTimer);
    return result;
  }

  return { draw: drawTask, check: checkSolution };
}
