import runTimer from '../taskTimer';
import wordsArr from './words';

export default function unnecessary() {
  const words = JSON.parse(JSON.stringify(wordsArr));
  const sample = [];
  let solution = '';
  const trueArr = words[Math.floor(Math.random() * words.length)];
  words.splice(words.indexOf(trueArr), 1);
  const falseArr = words[Math.floor(Math.random() * words.length)];

  function getRandom(arr) {
    const rNum = Math.floor(Math.random() * arr.length);
    const word = arr[rNum];
    arr.splice(rNum, 1);
    return word;
  }

  function shuffleArray(array) {
    for (let i = array.length - 1; i > 0; i -= 1) {
      const j = Math.floor(Math.random() * (i + 1));
      const temp = array[i];
      array[i] = array[j];
      array[j] = temp;
    }
  }

  function getSample() {
    const word1 = getRandom(trueArr);
    sample.push(word1);
    const word2 = getRandom(trueArr);
    sample.push(word2);
    const word3 = getRandom(trueArr);
    sample.push(word3);
    const word4 = getRandom(falseArr);
    solution = word4;
    sample.push(word4);
    return shuffleArray(sample);
  }

  function check() {
    this.classList.toggle('task__box--checked');
    document.querySelectorAll('.task__box--unnecessary').forEach((el) => {
      if (el.classList.contains('task__box--checked') && el !== this) {
        el.classList.toggle('task__box--checked');
      }
    });
  }

  getSample();

  function drawTask() {
    const taskBlock = document.createElement('div');
    taskBlock.setAttribute('class', 'game__modal game__modal--task');
    taskBlock.innerHTML = `
      <div class="task__timer">10</div>
      <div class="task__text">Найди лишнее слово:</div>
      <div class="task__boxes">
        <div class="task__box task__box--unnecessary">${sample[0]}</div>
        <div class="task__box task__box--unnecessary">${sample[1]}</div>
      </div>
      <div class="task__boxes">
        <div class="task__box task__box--unnecessary">${sample[2]}</div>
        <div class="task__box task__box--unnecessary">${sample[3]}</div>
      </div>
      <button class="game__btn game__btn--answer">Ответить</button>
    `;
    document.querySelector('.game__wrapper').appendChild(taskBlock);
    const btns = document.querySelectorAll('.task__box--unnecessary');
    btns.forEach(el => el.addEventListener('click', check));
    runTimer(6);
  }

  function checkSolution() {
    let result = 'enemyRight';
    if (document.querySelector('.task__box--checked').innerHTML === solution && document.querySelector('.task__timer').innerHTML === 'Время вышло!') {
      result = 'bothRight';
    } else if (document.querySelector('.task__box--checked').innerHTML === solution) {
      result = 'playerRight';
    }
    clearInterval(runTimer);
    return result;
  }

  return { draw: drawTask, check: checkSolution };
}
