export default function runTimer(time) {
  document.querySelector('.task__timer').innerHTML = time;

  // eslint-disable-next-line no-unused-vars
  let run = setTimeout(function tick() {
    if (document.querySelector('.task__timer')) {
      if (+document.querySelector('.task__timer').innerHTML !== 0) {
        document.querySelector('.task__timer').innerHTML -= 1;
        run = setTimeout(tick, 1000);
      } else {
        document.querySelector('.task__timer').innerHTML = 'Время вышло!';
      }
    }
  }, 1000);
}
