import task from './tasks/tasks';
import modal from './modals/modal';
import animate from './animations/animation';

export default (function game() {
  let playerName = '';
  let answer = false;
  const animations = animate();

  function showTask(type) {
    setTimeout(() => {
      const taskCommand = task(type);
      taskCommand.draw();
      document.querySelector('.game__btn--answer').addEventListener('click', () => {
        answer = taskCommand.check();
        document.querySelector('.game__wrapper').removeChild(document.querySelector('.game__modal'));
        // eslint-disable-next-line no-use-before-define
        animation(answer, taskCommand.ability);
      });
    }, 300);
  }

  function showAbility() {
    modal.showAbility();
    document.querySelector('.game__btn--attack').addEventListener('click', () => {
      showTask('simple');
      document.querySelector('.game__wrapper').removeChild(document.querySelector('.game__modal--ability'));
    });
    document.querySelector('.game__btn--heal').addEventListener('click', () => {
      showTask('heal');
      document.querySelector('.game__wrapper').removeChild(document.querySelector('.game__modal--ability'));
    });
    document.querySelector('.game__btn--extra').addEventListener('click', () => {
      showTask('extra');
      document.querySelector('.game__wrapper').removeChild(document.querySelector('.game__modal--ability'));
    });
  }


  function showFight() {
    document.querySelector('.game__plName--player').innerHTML = playerName;
    setTimeout(showAbility, 1000);
  }

  function getNameValue() {
    if (!document.querySelector('.game__input').value) {
      /* alert('Нужно ввести Ваше имя!'); */
    } else {
      playerName = document.querySelector('.game__input').value;
      localStorage.setItem('playerName', playerName);
      setTimeout(() => {
        document.querySelector('.game__wrapper').removeChild(document.querySelector('.game__modal'));
        showFight();
      }, 300);
    }
  }

  function start() {
    modal.showHPBars(playerName, animations.enemyName);
    modal.getName();
    const input = document.querySelector('.game__input');
    if (localStorage.getItem('playerName')) {
      input.value = localStorage.getItem('playerName');
    }
    input.focus();
    input.select();
    document.querySelector('.game__btn').addEventListener('click', getNameValue);
  }

  function startNewGame() {
    document.querySelector('.game__wrapper').removeChild(document.querySelector('.game__modal'));
    document.querySelector('.game__wrapper').removeChild(document.querySelector('.game'));
    animations.newPlayer();
    animations.nextRound();

    start();
  }

  function newGame() {
    modal.gameOver();
    document.querySelector('.game__btn--newGame').addEventListener('click', startNewGame);
  }

  function startNextRound() {
    const enemName = animations.nextRound();
    let round = document.querySelector('.game__round').innerHTML;
    round = 1 + Number(round);
    document.querySelector('.game__round').innerHTML = round;
    document.querySelector('.game__wrapper').removeChild(document.querySelector('.game__modal'));
    document.querySelector('.game__plName--enemy').innerHTML = enemName;
    document.querySelector('.game__health--enemy').style.width = '100%';
    setTimeout(() => {
      showAbility();
    }, 1000);
  }

  function nextRound() {
    modal.nextRound();
    document.querySelector('.game__btn--nextRound').addEventListener('click', startNextRound);
  }

  function animation(val, abil) {
    function showResult() {
      if (val === 'playerRight' || val === 'bothRight') {
        modal.showTrue();
      } else {
        modal.showFalse();
      }
    }

    if (val === 'playerRight') {
      if (abil === 'simple') {
        if (animations.enemyHP() > 10) {
          animations.attackHurt(showAbility, showResult);
        } else if (animations.enemyHP() <= 10) {
          animations.attackDie(nextRound, showResult);
        }
      } else if (abil === 'heal') {
        animations.playerHeal(showAbility, showResult);
      } else if (abil === 'extra') {
        /* заглушка */
        showAbility();
      }
    } else if (val === 'enemyRight') {
      if (animations.playerHP() > 10) {
        animations.hurtAttack(showAbility, showResult);
      } else if (animations.playerHP() <= 10) {
        animations.dieAttack(newGame, showResult);
      }
    } else if (val === 'bothRight') {
      if (abil === 'simple') {
        if (animations.playerHP() > 10 && animations.enemyHP() > 10) {
          animations.attackAttack(showAbility, showResult);
        } else if (animations.enemyHP() <= 10) {
          animations.attackDie(nextRound, showResult);
        } else {
          animations.attackDieAttack(showAbility, showResult);
        }
      } else if (abil === 'heal') {
        animations.healAttack(showAbility, showResult);
      } else if (abil === 'extra') {
        /* заглушка */
        showAbility();
      }
    }
  }

  return { start };
}());
