import enemyNames from './enemyNames';
import enemyParts from './enemyParts';

export default class EnemyAnimation {
  constructor() {
    this.sprite = undefined;
    this.ctx = undefined;
    this.parts = {
      rleg: {}, lleg: {}, body: {}, head: {}, larm: {}, rarm: {},
    };
    this.degr = 0;
    this.direction = 1;
    this.animationType = 'idle';
    this.nextAnimationType = 'idle';
    this.name = '';
    this.health = 100;
  }

  setName() {
    this.name = `${enemyNames.first[Math.floor(Math.random() * (enemyNames.first.length))]} ${enemyNames.second[Math.floor(Math.random() * (enemyNames.second.length))]} ${enemyNames.third[Math.floor(Math.random() * (enemyNames.third.length))]}`;
  }

  randomize() {
    this.parts.rleg = enemyParts.rlegs[Math.floor(Math.random() * (5))];
    this.parts.lleg = enemyParts.llegs[Math.floor(Math.random() * (5))];
    this.parts.body = enemyParts.bodys[Math.floor(Math.random() * (5))];
    this.parts.head = enemyParts.heads[Math.floor(Math.random() * (5))];
    this.parts.larm = enemyParts.larms[Math.floor(Math.random() * (5))];
    this.parts.rarm = enemyParts.rarms[Math.floor(Math.random() * (5))];
    this.health = 100;
  }

  drawPart(part) {
    // eslint-disable-next-line
    this.ctx.drawImage(this.sprite, part.posX, part.posY, part.szX,  part.szY, part.canvX, part.canvY, part.szX,  part.szY)
  }

  move() {
    if (this.nextAnimationType === 'hurt') {
      if (this.animationType === 'hurt') {
        if (this.degr === 5 && this.direction === 1 && this.animationType === 'hurt') {
          const audio = new Audio();
          audio.src = require('../../sounds/enemyHurt.wav');
          audio.loop = false;
          audio.play();
        }
        this.ctx.clearRect(500, 100, 300, 300);
        this.drawPart(this.parts.rleg); // правая нога
        this.drawPart(this.parts.lleg); // левая ноганоги

        this.ctx.save(); // поворот правой руки
        this.ctx.translate(646, 184);
        this.ctx.rotate((this.degr) * 1 * Math.PI / 180);
        this.drawPart(this.parts.rarm); // правая рука
        this.ctx.restore();

        this.drawPart(this.parts.body); // тело

        this.ctx.save(); // поворот левой руки
        this.ctx.translate(716, 184);
        this.ctx.rotate(-this.degr * 1 * Math.PI / 180);
        this.drawPart(this.parts.larm); // левая рука
        this.ctx.restore();

        this.ctx.save(); // поворот головы
        this.ctx.translate(679, 174);
        this.ctx.rotate(this.degr * 1 * Math.PI / 180);
        this.drawPart(this.parts.head); // голова
        this.ctx.restore();

        if (this.degr >= 10) {
          this.direction = -5;
        } else if (this.degr <= 0) {
          this.direction = 0;
          this.degr = 0;
          this.animationType = 'idle';
          this.nextAnimationType = 'idle';
        }
        this.degr += this.direction;
      } else if (this.animationType === 'idle') {
        this.animationType = 'hurt';
        this.degr = 5;
        this.direction = 1;
      }
    } else if (this.animationType === 'idle') {
      this.ctx.clearRect(500, 100, 300, 300);
      this.drawPart(this.parts.rleg); // правая нога
      this.drawPart(this.parts.lleg); // левая ноганоги

      this.ctx.save(); // поворот правой руки
      this.ctx.translate(646, 184);
      this.ctx.rotate((-15 + this.degr) * 0.5 * Math.PI / 180);
      this.drawPart(this.parts.rarm); // правая рука
      this.ctx.restore();

      this.drawPart(this.parts.body); // тело

      this.ctx.save(); // поворот левой руки
      this.ctx.translate(716, 184);
      this.ctx.rotate(-this.degr * 0.5 * Math.PI / 180);
      this.drawPart(this.parts.larm); // левая рука
      this.ctx.restore();

      this.ctx.save(); // поворот головы
      this.ctx.translate(679, 174);
      this.ctx.rotate(-this.degr * 0.5 * Math.PI / 180);
      this.drawPart(this.parts.head); // голова
      this.ctx.restore();

      if (this.degr === 10) {
        this.direction = -1;
        this.degr += this.direction * 2;
      } else if (this.degr === 0) {
        if (this.nextAnimationType === 'attack') {
          this.animationType = 'attack';
          this.direction = 1;
          this.degr = 0;
        } else {
          this.direction = 1;
          this.degr += this.direction * 2;
        }
      } else {
        this.degr += this.direction * 2;
      }
    } else if (this.animationType === 'attack') {
      if (this.degr === 7.5) {
        const audio = new Audio();
        audio.src = require('../../sounds/enemyAttack.wav');
        audio.loop = false;
        audio.play();
      }
      this.ctx.clearRect(500, 50, 300, 350);
      this.drawPart(this.parts.rleg); // правая нога
      this.drawPart(this.parts.lleg); // левая ноганоги

      this.ctx.save(); // поворот правой руки
      this.ctx.translate(646, 184);
      this.ctx.rotate((0 + this.degr) * 12 * Math.PI / 180);
      this.drawPart(this.parts.rarm); // правая рука
      this.ctx.restore();

      this.drawPart(this.parts.body); // тело

      this.ctx.save(); // поворот левой руки
      this.ctx.translate(716, 184);
      this.ctx.rotate(-this.degr * Math.PI / 180);
      this.drawPart(this.parts.larm); // левая рука
      this.ctx.restore();

      this.ctx.save(); // поворот головы
      this.ctx.translate(679, 174);
      this.ctx.rotate(-this.degr * 0.5 * Math.PI / 180);
      this.drawPart(this.parts.head); // голова
      this.ctx.restore();

      if (this.degr === 10) {
        this.direction = -1;
        this.degr += this.direction * 10;
      } else if (this.degr === 0) {
        if (this.nextAnimationType === 'attack' && this.animationType === 'attack') {
          this.direction = 1;
          this.degr += this.direction * 2.5;
          this.nextAnimationType = 'idle';
        } else if (this.nextAnimationType === 'idle' && this.animationType === 'attack') {
          this.direction = 1;
          this.degr += this.direction * 2;
          this.animationType = 'idle';
        }
      } else {
        this.degr += this.direction * 2.5;
      }
    } else if (this.animationType === 'die') {
      this.ctx.clearRect(500, 100, 300, 300);
      this.drawPart(this.parts.rleg); // правая нога
      this.drawPart(this.parts.lleg); // левая ноганоги

      this.ctx.save(); // поворот правой руки
      this.ctx.translate(646, 184);
      this.drawPart(this.parts.rarm); // правая рука
      this.ctx.restore();

      this.drawPart(this.parts.body); // тело

      this.ctx.save(); // поворот левой руки
      this.ctx.translate(716, 184);
      this.drawPart(this.parts.larm); // левая рука
      this.ctx.restore();

      this.ctx.save(); // поворот головы
      this.ctx.translate(679, 174 + this.degr * 0.7);
      this.ctx.rotate((this.degr) * Math.PI / 180);
      this.drawPart(this.parts.head); // голова
      this.ctx.restore();

      if (this.degr >= 150) {
        this.direction = 0;
      } else if (this.degr === 0) {
        this.direction = 30;
      } else {
        this.direction = 30;
      }
      this.degr += this.direction;
    }
  }

  enemyMove() {
    setInterval(this.move.bind(this), 100);
  }
}
