import simplemath from './simpleMath/simpleMath';
import translation from './engToRu/engToRu';
import sequence from './sequence/sequence';
import unnecessary from './unnecessary/unnecessary';
import pictures from './pictures/pictures';
import auding from './auding/auding';
/* import compare from './compare/compare'; */

export default function taskFunction(type) {
  const ability = type;

  const tasks = {
    1: simplemath,
    2: translation,
    3: sequence,
    4: unnecessary,
    5: pictures,
    6: auding,
  };

  function randomInt(min, max) {
    return Math.floor(Math.random() * (max - min)) + min;
  }

  function getTask() {
    const randomFunction = randomInt(1, 7);
    const check = tasks[randomFunction]();
    return check;
  }

  const task = getTask();
  return { draw: task.draw, check: task.check, ability };
}
