import PlayerAnimation from './player/playerAnimation';
import EnemyAnimation from './enemy/enemyAnimation';

export default function animate() {
  const player = new PlayerAnimation();
  const enemy = new EnemyAnimation();

  function enemyHurt() { enemy.nextAnimationType = 'hurt'; }
  function enemyAttack() { enemy.nextAnimationType = 'attack'; }
  function enemyDie() { enemy.animationType = 'die'; }
  function playerHurt() { player.nextAnimationType = 'hurt'; }
  function playerAttack() { player.nextAnimationType = 'attack'; }
  function playerDie() { player.nextAnimationType = 'die'; }
  function playerHeal() { player.nextAnimationType = 'heal'; }
  function getEnemyHp() { return enemy.health; }
  function getPlayerHp() { return player.health; }
  function run() {
    player.startAnimation();
    window.requestAnimationFrame(() => {
      run();
    });
  }
  function startAnimation() {
    const canvas = document.querySelector('.canvas');
    player.ctx = canvas.getContext('2d');
    enemy.ctx = canvas.getContext('2d');
    enemy.sprite = new Image();
    enemy.sprite.src = require('../../images/enemy.png');
    run();
  }

  enemy.randomize();
  enemy.setName();
  startAnimation();
  player.move();
  enemy.enemyMove();

  function promiseDelay(action, delay) {
    return new Promise((resolve) => {
      setTimeout(() => {
        action();
        resolve();
      }, delay);
    });
  }

  const playerAttackEnemyHurt = (step, result) => {
    const promise = promiseDelay(result, 300);
    promise
      .then(() => promiseDelay(playerAttack, 1500))
      .then(() => promiseDelay(enemyHurt, 1200))
      .then(() => {
        enemy.health -= 10;
        document.querySelector('.game__health--enemy').style.width = `${enemy.health}%`;
        return promiseDelay(step, 2000);
      });
  };

  const heal = (step, result) => {
    const promise = promiseDelay(result, 300);
    promise
      .then(() => promiseDelay(playerHeal, 1500))
      .then(() => {
        if (player.health <= 90) {
          player.health += 10;
          document.querySelector('.game__health--player').style.width = `${player.health}%`;
        }
        return promiseDelay(step, 2000);
      });
  };

  const playerHealenemyAttack = (step, result) => {
    const promise = promiseDelay(result, 300);
    promise
      .then(() => promiseDelay(playerHeal, 1500))
      .then(() => {
        if (player.health <= 90) {
          player.health += 10;
          document.querySelector('.game__health--player').style.width = `${player.health}%`;
        }
      })
      .then(() => promiseDelay(enemyAttack, 2000))
      .then(() => promiseDelay(playerHurt, 1000))
      .then(() => {
        player.health -= 10;
        document.querySelector('.game__health--player').style.width = `${player.health}%`;
        return promiseDelay(step, 2000);
      });
  };

  const playerAttackEnemyDie = (step, result) => {
    const promise = promiseDelay(result, 300);
    promise
      .then(() => promiseDelay(playerAttack, 1500))
      .then(() => promiseDelay(enemyDie, 2000))
      .then(() => {
        enemy.health -= 10;
        document.querySelector('.game__health--enemy').style.width = `${enemy.health}%`;
        return promiseDelay(step, 2000);
      });
  };

  const enemyAttackPlayerHurt = (step, result) => {
    const promise = promiseDelay(result, 300);
    promise
      .then(() => promiseDelay(enemyAttack, 1200))
      .then(() => promiseDelay(playerHurt, 300))
      .then(() => promiseDelay(() => {
        player.health -= 10;
        document.querySelector('.game__health--player').style.width = `${player.health}%`;
      }, 1000))
      .then(() => promiseDelay(step, 2000));
  };

  const enemyAttackPlayerDie = (step, result) => {
    const promise = promiseDelay(result, 300);
    promise
      .then(() => promiseDelay(enemyAttack, 1200))
      .then(() => promiseDelay(playerDie, 1000))
      .then(() => {
        player.health -= 10;
        player.health -= 10;
        document.querySelector('.game__health--player').style.width = 0;
        return promiseDelay(step, 2000);
      });
  };

  const playerAttackEnemyAttack = (step, result) => {
    const promise = promiseDelay(result, 300);
    promise
      .then(() => promiseDelay(playerAttack, 1200))
      .then(() => promiseDelay(enemyHurt, 1000))
      .then(() => {
        enemy.health -= 10;
        document.querySelector('.game__health--enemy').style.width = `${enemy.health}%`;
        return promiseDelay(enemyAttack, 2000);
      })
      .then(() => promiseDelay(playerHurt, 1000))
      .then(() => {
        player.health -= 10;
        document.querySelector('.game__health--player').style.width = `${player.health}%`;
        return promiseDelay(step, 2000);
      });
  };

  const playerAttackEnemyAttackPlayerDie = (step, result) => {
    const promise = promiseDelay(result, 300);
    promise
      .then(() => promiseDelay(playerAttack, 1200))
      .then(() => promiseDelay(enemyHurt, 1000))
      .then(() => {
        enemy.health -= 10;
        document.querySelector('.game__health--enemy').style.width = `${enemy.health}%`;
        return promiseDelay(enemyAttack, 1000);
      })
      .then(() => {
        player.health -= 10;
        document.querySelector('.game__health--player').style.width = 0;
        return promiseDelay(playerDie, 1000);
      })
      .then(() => promiseDelay(step, 2000));
  };

  function getNextEnemy() {
    enemy.randomize();
    enemy.setName();
    enemy.animationType = 'idle';
    enemy.degr = 0;
    enemy.direction = 1;
    return enemy.name;
  }
  function getNewPlayer() {
    player.frame = 1;
    player.health = 100;
    player.animationType = 'idle';
    player.nextAnimationType = 'idle';
  }

  const animations = {
    enemyName: enemy.name,
    enemyHP: getEnemyHp,
    playerHP: getPlayerHp,
    attackHurt: playerAttackEnemyHurt,
    playerHeal: heal,
    healAttack: playerHealenemyAttack,
    attackDie: playerAttackEnemyDie,
    hurtAttack: enemyAttackPlayerHurt,
    dieAttack: enemyAttackPlayerDie,
    attackAttack: playerAttackEnemyAttack,
    attackDieAttack: playerAttackEnemyAttackPlayerDie,
    nextRound: getNextEnemy,
    newPlayer: getNewPlayer,
  };

  return animations;
}
