import runTimer from '../taskTimer';
import words from './words';

export default function task() {
  function randomInt(length) {
    return Math.floor(Math.random() * length);
  }

  // eslint-disable-next-line no-unused-vars
  function shaker(a, b) {
    return Math.random() - 0.5;
  }

  const word = words[randomInt(words.length)];
  const array = word.split('').sort(shaker).sort(shaker);
  const markup = array.reduce((acc, el) => {
    acc += `<div class="task__box">${el}</div> `;
    return acc;
  }, '');
  let solution = array;

  function dragStart(e) {
    const block = e.target;
    const { top } = block.getBoundingClientRect();
    const { left } = block.getBoundingClientRect();
    document.body.appendChild(block);
    block.style.top = `${top - 5}px`;
    block.style.backgroundColor = 'rgba(179, 255, 0, 0.596)';
    block.style.left = `${left - 4}px`;
    block.setAttribute('class', 'task__box task__box--moved');
    block.style.zIndex = 3;
    const blockPos = e.pageX - left;
    const empty = document.createElement('div');
    empty.setAttribute('class', 'task__box task__box--empty');
    empty.style.order = block.style.order;
    empty.innerHTML = block.innerHTML;
    empty.addEventListener('mousedown', dragStart);
    document.querySelector('.task__boxes').appendChild(empty);
    let pos = e.pageX;

    // eslint-disable-next-line no-shadow
    function move(e) {
      const blocks = document.querySelectorAll('.task__box');
      if (e.pageX - pos > 0) {
        pos = e.pageX;
        blocks.forEach((el) => {
          if (el.parentElement !== document.body) {
            if (+el.style.order === +empty.style.order + 1) {
              if (+el.getBoundingClientRect().left < +block.getBoundingClientRect().left + 22) {
                el.style.order = +el.style.order - 1;
                empty.style.order = +empty.style.order + 1;
              }
            }
          }
        });
      } else if (e.pageX - pos < 0) {
        pos = e.pageX;
        blocks.forEach((el) => {
          if (el.parentElement !== document.body) {
            if (+el.style.order === +empty.style.order - 1) {
              if (+el.getBoundingClientRect().left > +block.getBoundingClientRect().left - 21) {
                el.style.order = +el.style.order + 1;
                empty.style.order = +empty.style.order - 1;
              }
            }
          }
        });
      }

      block.style.left = `${e.pageX - blockPos}px`;

      document.addEventListener('mouseup', () => {
        empty.setAttribute('class', 'task__box');
        document.removeEventListener('mousemove', move);
        document.querySelectorAll('.task__box').forEach((el) => {
          if (el.parentElement === document.body) document.body.removeChild(el);
        });
      });
    }

    document.addEventListener('mousemove', move);
  }

  function drawTask() {
    const taskBlock = document.createElement('div');
    taskBlock.setAttribute('class', 'game__modal game__modal--task');
    taskBlock.innerHTML = `
      <div class="task__timer">10</div>
      <div class="task__text task__text--wm">Переставь буквы,</div>
      <div class="task__text task__text--wm">чтобы получить слово:</div>
      <div class="task__boxes">${markup}</div>
      <button class="game__btn game__btn--answer">Проверить</button>
    `;
    document.querySelector('.game__wrapper').appendChild(taskBlock);
    runTimer(20);
    document.querySelectorAll('.task__box').forEach((el, ind) => {
      el.addEventListener('mousedown', dragStart);
      el.style.order = ind;
    });
  }

  function checkSolution() {
    let result = 'enemyRight';
    const arr = [];
    solution = document.querySelectorAll('.task__box');
    solution.forEach((el) => {
      arr[el.style.order] = el.innerHTML;
    });
    solution = arr.join('');
    if (solution === word && document.querySelector('.task__timer').innerHTML === 'Время вышло!') {
      result = 'bothRight';
    } else if (solution === word) {
      result = 'playerRight';
    }
    clearInterval(runTimer);
    return result;
  }

  return { draw: drawTask, check: checkSolution };
}
