import playerconfig from './playerconfig';

export default class PlayerAnimation {
  constructor() {
    this.health = 100;
    this.sprite = new Image();
    this.sprite.src = require('./../../../images/player.png');
    this.positions = playerconfig;
    this.frame = 1;
    this.animationType = 'idle';
    this.nextAnimationType = 'idle';
  }

  startAnimation() {
    this.ctx.clearRect(30, 145, 250, 200);
    // eslint-disable-next-line max-len
    this.ctx.drawImage(this.sprite, this.positions[this.animationType].imX, this.positions[this.animationType].imY, this.positions[this.animationType].szX, this.positions[this.animationType].szY, this.positions[this.animationType].posX, this.positions[this.animationType].posY, this.positions[this.animationType].szX, this.positions[this.animationType].szY);
  }

  move() {
    setInterval(() => {
      if (this.animationType === 'attack' && this.frame === 2) {
        const audio = new Audio();
        audio.src = require('../../sounds/playerAttack.wav');
        audio.loop = false;
        audio.play();
      }
      if (this.animationType === 'hurt' && this.frame === 3) {
        const audio = new Audio();
        audio.src = require('../../sounds/playerHurt.wav');
        audio.loop = false;
        audio.play();
      }
      if (this.animationType === 'heal' && this.frame === 1) {
        const audio = new Audio();
        audio.src = require('../../sounds/playerHeal.wav');
        audio.loop = false;
        audio.play();
      }
      if (this.nextAnimationType === 'die' && this.animationType === 'idle') {
        this.positions[this.animationType].imX += this.positions[this.animationType].change;
        this.frame += 1;
        if (this.frame === 6) {
          this.positions[this.animationType].imX = 0;
          this.frame = 1;
          this.animationType = this.nextAnimationType;
        }
      } else if (this.nextAnimationType === 'die' && this.animationType === 'die') {
        if (this.frame !== 5) {
          this.frame += 1;
          this.positions[this.animationType].imX += this.positions[this.animationType].change;
        }
      } else if (this.nextAnimationType !== 'idle') {
        this.positions[this.animationType].imX += this.positions[this.animationType].change;
        this.frame += 1;
        if (this.frame === 6) {
          this.positions[this.animationType].imX = 0;
          this.frame = 1;
          this.animationType = this.nextAnimationType;
          this.nextAnimationType = 'idle';
        }
      } else {
        this.positions[this.animationType].imX += this.positions[this.animationType].change;
        this.frame += 1;
        if (this.frame === 6) {
          this.positions[this.animationType].imX = 0;
          this.frame = 1;
          this.animationType = 'idle';
        }
      }
    }, 200);
  }
}
