import runTimer from '../taskTimer';
import samples from './samples';

export default function unnecessary() {
  const samplesBase = samples();
  const samplesArr = samplesBase[0];
  const num = Math.floor(Math.random() * samplesArr.length);
  const sample = samplesArr[num];
  const solution = samplesBase[1][num];

  function drawTask() {
    const taskBlock = document.createElement('div');
    taskBlock.setAttribute('class', 'game__modal game__modal--task');
    taskBlock.innerHTML = `
      <div class="task__timer">10</div>
      <div class="task__text task__text--wm">Что на картинке?</div>
      <div class="task__text task__text--wm">(на английском)</div>
      <input class="game__input" type="text">
      <button class="game__btn game__btn--answer">Проверить</button>
      <div class="task__imageWrapper">
        <img class="task__image" src="${sample.src}" alt="">
      </div>
      
    `;
    document.querySelector('.game__wrapper').appendChild(taskBlock);
    document.querySelector('.task__image').style.backgroundImage = sample;
    runTimer(10);
  }

  function checkSolution() {
    let result = 'enemyRight';
    const playerSolution = document.querySelector('.game__input').value;
    if (playerSolution === solution && document.querySelector('.task__timer').innerHTML === 'Время вышло!') {
      result = 'bothRight';
    } else if (playerSolution === solution) {
      result = 'playerRight';
    }
    return result;
  }

  return { draw: drawTask, check: checkSolution };
}
