export default {
  getName() {
    const gName = document.createElement('div');
    gName.setAttribute('class', 'game__modal');
    gName.innerHTML = `
      <span class="game__text">Введи свое имя,</span>
      <span class="game__text game__text--nomargin">Герой!</span>
      <input class="game__input"type="text">
      <button class="game__btn">В бой!</button>
    `;
    document.querySelector('.game__wrapper').appendChild(gName);
  },

  showAbility() {
    const abil = document.createElement('div');
    abil.setAttribute('class', 'game__modal game__modal--ability');
    abil.innerHTML = `
      <p class="game__text game__text--nomargin game__text--white">Выбери способность!</p>
      <div class="game__btnBlock">
        <button class="game__btn game__btn--attack">Атака</button>
        <button class="game__btn game__btn--heal">Лечение</button>
        <button class="game__btn game__btn--extra">Ярость</button>
      </div>
    `;
    document.querySelector('.game__wrapper').appendChild(abil);
  },

  showHPBars(plName, enName) {
    const hbar = document.createElement('div');
    hbar.setAttribute('class', 'game');
    hbar.innerHTML = `
      <div class="game__hp-block">
        <div class="game__playerSpace">
          <div class="game__healthbar">
            <div class="game__health game__health--player style="width: ${100}%"></div>
          </div>
          <span class="game__plName game__plName--player">${plName}</span>
        </div>
        <div class="game__round">1</div>
        <div class="game__playerSpace">
          <div class="game__healthbar">
            <div class="game__health game__health--enemy" style="width: ${100}%"></div>
          </div>
          <span class="game__plName game__plName--enemy">${enName}</span>
        </div>
      </div>
    `;
    document.querySelector('.game__wrapper').appendChild(hbar);
  },

  gameOver() {
    let round = Number(document.querySelector('.game__round').innerHTML);
    round -= 1;
    const numbers = ['1 врага', '2 врагов', '3 врагов', '4 врагов', '5 врагов', '6 врагов', '7 орков', '8 врагов', '9 врагов', '10 врагов', '11 врагов', '12 врагов', '13 врагов', '14 врагов', '15 врагов'];
    let text = '';
    if (round > 0) {
      text = `Ты уничтожил ${numbers[round - 1]}`;
    } else {
      text = 'Ты не одолел ни одного врага!';
    }
    const gOver = document.createElement('div');
    gOver.setAttribute('class', 'game__loose game__modal--loose');
    gOver.innerHTML = `
      <p class="task__text">${text}</p>
      <p class="task__text">Попробуй еще раз!</p>
      <button class="game__btn game__btn--newGame">Начать заново!</button>
    `;
    document.querySelector('.game__wrapper').appendChild(gOver);
  },

  nextRound() {
    const nRound = document.createElement('div');
    nRound.setAttribute('class', 'game__modal');
    nRound.innerHTML = `
      <p class="task__text task__text--top">Ты победил!</p>
      <p class="task__text">Следующий враг наступает!</p>
      <button class="game__btn game__btn--nextRound">В бой!</button>
    `;
    document.querySelector('.game__wrapper').appendChild(nRound);
  },

  showTrue() {
    const nRound = document.createElement('div');
    nRound.setAttribute('class', 'game__trueFlag');
    nRound.innerHTML = `
      <p class="game__text game__text--status">Правильно</p>
    `;
    document.querySelector('.game__wrapper').appendChild(nRound);
    setTimeout(() => {
      document.querySelector('.game__wrapper').removeChild(nRound);
    }, 1000);
  },

  showFalse() {
    const nRound = document.createElement('div');
    nRound.setAttribute('class', 'game__falseFlag');
    nRound.innerHTML = `
      <p class="game__text game__text--status">Не верно!</p>
    `;
    document.querySelector('.game__wrapper').appendChild(nRound);
    setTimeout(() => {
      document.querySelector('.game__wrapper').removeChild(nRound);
    }, 1000);
  },
};
