import words from './words';
import runTimer from '../taskTimer';

export default function task() {
  function randomInt(min, max) {
    return Math.floor(Math.random() * (max - min)) + min;
  }

  const sample = words.arr[randomInt(0, words.arr.length)];

  function drawTask() {
    const taskBlock = document.createElement('div');
    taskBlock.setAttribute('class', 'game__modal game__modal--task');
    taskBlock.innerHTML = `
      <div class="task__timer">10</div>
      <div class="task__text">Переведи слово :</div>
      <div class="task__example">
        <span class="task__text">${sample[0]}</span>
      </div>
      <input class="game__input"type="text">
      <button class="game__btn game__btn--answer">Ответить</button>
    `;
    document.querySelector('.game__wrapper').appendChild(taskBlock);
    runTimer(15);
  }

  function checkSolution() {
    let result = 'enemyRight';
    const playerSolution = document.querySelector('.game__input').value;
    if (sample.indexOf(playerSolution, 1) !== -1 && document.querySelector('.task__timer').innerHTML === 'Время вышло!') {
      result = 'bothRight';
    } else if (sample.indexOf(playerSolution, 1) !== -1) {
      result = 'playerRight';
    }
    return result;
  }

  return { draw: drawTask, check: checkSolution };
}
